/* src/components/CountdownTimer.css */
.countdown-timer {
    font-family: 'Arial', sans-serif;
    font-size: 2rem;
    font-weight: bold;
    color: #fff;
    text-align: center;
    padding: 10px;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  .countdown-timer:hover{
    background-color: white;
    color: black;
  }
  