/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

/* Side Menu Styles */
.side-menu {
  width: 250px;
  background-color: #f4f4f4;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  overflow: auto;
  transition: transform 0.3s ease;
}

.side-menu.hidden {
  transform: translateX(-100%);
}

.side-menu h2 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

/* Banner Styles */
.banner {
  margin-left: 250px; /* Adjust if you need spacing */
  padding: 20px;
  background: url('../images/dash1.jfif') no-repeat center center;
  background-size: cover;
  color: white;
  text-align: center;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner.hidden {
  display: none;
}

.banner h1 {
  font-size: 2em;
}

/* Input and Button Styles */
input, button {
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
}

input {
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
/* Default Styles */
.container {
  display: flex;
  flex-direction: row;
}

/* Media Queries */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }

  .side-menu {
    width: 100%;
    transform: translateX(0);
  }

  .banner {
    margin-left: 0;
    height: auto;
  }
}
