/* src/components/Banner.css */
.banner {
    position:relative;
    display: flex;
    flex-direction: column;
    top: 0;
    right: 0;
    width: 75vw;
    height: 100vh;
    padding: 20px;
    margin: 10px;
    background-image: url("../images/bgc.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    text-align: center;
    z-index: 1000;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
    animation: fadeIn 1s ease-out;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
  }
  
  .banner-content {
    max-width: 100%;
  }
  .banner-content h1{
    color: White;
  }
  .banner-content h1:hover{
    color: Black;
  }

  
  .banner-link {
    display: inline-block;
    margin-top: 10px;
    padding: 10px 20px;
    background: #f39c12;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
    font-weight: bold;
  }
  
  .banner-link:hover {
    background: #e67e22;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateX(20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  